export default function stakeHolder(role) {
    switch (role) {
        case '00':
            role = '()';
            break;
        case '01':
            role = '';
            break;
        case '02':
            role = '';
            break;
        case '10':
            role = '';
            break;
        case '11':
            role = '';
            break;
        case '20':
            role = '()';
            break;
        case '21':
            role = '()';
            break;
        case '22':
            role = '';
            break;
        case '30':
            role = '';
            break;
        default:
            break;
    }
    return role;
}


