export default function venderProductStatus(status, type) {
    const allStatus = {
        online: {
            '2': ''
        },
        offline: {
            '0': '',
            '1': '',
            '3': '',
            '4': '',
        }
    }

    return type ? allStatus[type][status] : status
}


