export default function supplierRank(level) {
    switch (level) {
        case 'N':
            level = '';
            break;
        case 'A':
            level = 'A';
            break;
        case 'B':
            level = 'B';
            break;
        case 'C':
            level = 'C';
            break;
        case 'D':
            level = 'D';
            break;
        default:
            break;
    }
    return level;
}


