import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
// state ()
// getters (state)
// actions (mutations)
// mutations (state)
const state = {
    havedClickPage:false,//
    oldBroastData:'',//
    nowModalTypeId:'',//id
    nowModelDirectice:'',//
    routerOpreteTo:'',
    routerOpreteFrom:'',
    routerOprete:'',// 
    AdroidOldMsg:'',
    AdroidNewMsg:'',
    nowRouter:'',
    globalGetConnectStatus:{},// 
    getConnectStatus:'',//
    weldingStatus:0,//0 1
    getWeldingInfoTimes:0,//
    weldingCur:'',//
    weldingVoltage:'',//
    weldingInfo:{},//
    weldingDelay:false,//8 
    nowConnectMachine:'',
    nowConnectAddress:'',
    rstInfo:{},//
    memoryInfo:{},//memory
    allowBack:true,
    hasPoup:true,
    isLogin: 0,
    LoginedUser:{
        'custId':'',
        'custName':'',
        'custType':'',
        'corName':'',
        'deptCode':'',
        'deptName':'',
        'userCode':'',
        'userMobile':'',
        'userName':'',
        'wxUuid':'',
        'headImage':''
    },
    purchasList:[],
    envType:'',//
    nowPageFrom:'',
    updateBlelistDB:[],//
    tigManChooseLineKey:'',
    callMemoryRemarks:[],
    rizhiList:[],
    rizhiListFlag:true,
    saveManagePageTo:'',
    saveManagePageToName:'',
    testModalDoorFlag:false,
    postDataList:[],
    nowLoginStauts:'',
    isModbusModal:false,
    modbusSendTimes:0,
    modbusSendDataTimes:0,
    logFaceFlag:false,//
    modbusCircleTimer:{},
    email:'',
    password:'',
    userUuid:'',
    weldingInterval:{},//
    momeryClickNum:'',
    momeryApplyModel:''// modbus
    

}

const mutations = {
    changeLogin(state,status){
        // sessionStorage
        if(status=='1'){
            let user = JSON.parse(sessionStorage.getItem('user'))
            console.log(user)
            // 
            state.isLogin = user.isLogin;
            state.LoginedUser.custId = user.custId
            state.LoginedUser.custName = user.custName
            state.LoginedUser.custType = user.custType
        }
    },

    changePurchase(state,purchasList){
        state.purchasList = purchasList
    }
}

const actions = {
    loginAction({commit},status){
        console.log('8888888'+status)
        console.log(JSON.parse(sessionStorage.getItem('user')))
        commit('changeLogin',status);
    },

    purchaseAction({commit},purchasList){
        console.dir(purchasList)
        commit('changePurchase',purchasList);
    }
}

export default new Vuex.Store({
    state,
    actions,
    mutations
})


