import fillZero from './fillZero';
export default function weekDay(_date) {
    if (!_date) {
        return _date;
    }
    const date = new Date(_date);
    const week = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
    }
    return week[date.getDay()];
}


