export default function stakeHolderSign(role) {
    switch (role) {
        case '00':
            role = '';
            break;
        case '01':
            role = '';
            break;
        case '02':
            role = '';
            break;
        case '10':
            role = '';
            break;
        case '11':
            role = '';
            break;
        case '20':
            role = '()';
            break;
        case '21':
            role = '';
            break;
        case '22':
            role = '';
            break;
        case '25':
            role = '';
            break;
        case '26':
            role = '';
            break;
        case '27':
            role = '';
            break;
        case '30':
            role = '';
            break;
        case '31':
            role = '';
            break;
        case '32':
            role = '';
            break;
        case '33':
            role = '';
            break;
        case '43':
            role = '';
            break;
        case '44':
            role = '';
            break;
        default:
            break;
    }
    return role;
}


